import React, { Component } from "react";
import { Button } from 'react-md';
import CardTitle from "react-md/lib/Cards/CardTitle";
import Avatar from "react-md/lib/Avatars";
import FontIcon from "react-md/lib/FontIcons";
import moment from "moment";
import _ from "lodash";
import config from "../../../data/SiteConfig";
import "./PostInfo.scss";

class PostInfo extends Component {
  constructor(props) {
    super(props);
    this.weekdayKo = this.weekdayKo.bind(this);
    this.weekdays = {
      "Monday" : "월",
      "Tuesday" : "화",
      "Wednesday" : "수",
      "Thursday" : "목",
      "Friday" : "금",
      "Saturday" : "토",
      "Sunday" : "일",
    }
  }
  weekdayKo(wkDay) {
    return `${this.weekdays[wkDay]}요일`;
  };
  render() {
    const { postNode } = this.props;
    const post = postNode.frontmatter;
    return (
      <div className="post-info">
        <CardTitle
          avatar={<Avatar icon={<FontIcon iconClassName="fa fa-calendar" />} />}
          title="시작일"
          subtitle={`${moment(post.startAt).format(
            config.dateFormatForKo
          )} ${this.weekdayKo(moment(post.startAt).format(config.dateFormatWeekday))}`}
        />
        <CardTitle
          avatar={
            <Avatar icon={<FontIcon iconClassName="fa fa-home" />} />
          }
          title="수업 장소"
          subtitle={post.place}
        />
        <a
          href="https://goo.gl/JnHCUy"
          className="link-googleform"
          target="_blank"
          rel="noopener noreferrer"
        >
          <Button raised secondary iconBefore={false} iconClassName="fa fa-hand-paper-o">
            신청하기
          </Button>
        </a>
      </div>
    );
  }
}

export default PostInfo;



