import React, { Component } from "react";
import Masonry from 'react-masonry-css';
import './PostMasonry.scss';

class PostMasonry extends Component {
  // constructor(props) {
  //   super(props);
  // }
  render() {
    const breakpointColumnsObj = {
      default: 3,
      1100: 3,
      700: 2,
      400: 2
    };
    return (
      <Masonry
        breakpointCols={breakpointColumnsObj}
        className="my-masonry-grid"
        columnClassName="my-masonry-grid_column"
      >
        <div>
          <img src={`/assets/${this.props.images[0]}`} alt="클래스 안내 이미지" />
        </div>
        <div>
          <img src="https://picsum.photos/480/640/?random" alt="클래스 안내 이미지" />
        </div>
        <div>
          <img src="https://picsum.photos/640/480/?random" alt="클래스 안내 이미지" />
        </div>
        <div>
          <img src={`/assets/${this.props.images[1]}`} alt="클래스 안내 이미지" />
        </div>
        <div>
          <img src={`/assets/${this.props.images[2]}`} alt="클래스 안내 이미지" />
        </div>
        <div>
          <img src={`/assets/${this.props.images[3]}`} alt="클래스 안내 이미지" />
        </div>
        <div>
          <img src="https://picsum.photos/480/640/?random" alt="클래스 안내 이미지" />
        </div>
        <div>
          <img src={`/assets/${this.props.images[4]}`} alt="클래스 안내 이미지" />
        </div>
        <div>
          <img src="https://picsum.photos/480/720/?random" alt="클래스 안내 이미지" />
        </div>
      </Masonry>
    );
  }
}

export default PostMasonry;


